import { computed, inject, reactive, ref } from "vue";

export function useBloodTestsReportInit() {
    const dialogVisible = inject("dialogVisible");
    return { dialogVisible };
}

export function useBloodTestReportInit() {
    const dialogVisible = inject("dialogVisible");
    return { dialogVisible };
}

export function useBloodTestDailogInit() {
    const dialogVisible = inject("dialogVisible");
    return { dialogVisible };
}

export function useBloodTestGroupSelectionInit() {
    const bloodTests = [
        {
            groupName: "Testosterone",
            groupCode: "testosterone",
            code: 9,
            name: "TEST 9"
        },
        {
            groupName: "Testosterone",
            groupCode: "testosterone",
            code: 8,
            name: "TEST 8"
        },
        {
            groupName: "AMHP",
            groupCode: "amhp",
            code: 7,
            name: "TEST 7"
        },
        {
            groupName: "Day 3",
            groupCode: "day3",
            code: 1,
            name: "TEST 1"
        },
        {
            groupName: "Monitoring",
            groupCode: "monitoring",
            code: 2,
            name: "TESTO 2"
        }
    ];

    const testGroup = ref([]);
    const testGroupsOptions = reactive([
        {
            label: "Testosterone",
            code: "testosterone"
        },
        {
            label: "Day 3",
            code: "day3"
        },
        {
            label: "Monitoring",
            code: "monitoring"
        },
        {
            label: "AMHP",
            code: "amhp"
        },
        {
            label: "All",
            code: "all"
        }
    ]);
    const testSelectionTable = computed(() => {
        if (testGroup.value.length == 0) return [];
        else if (testGroup.value.includes("all")) {
            return bloodTests;
        } else {
            return bloodTests.filter((item) =>
                testGroup.value.includes(item.groupCode)
            );
        }
    });
    const seletedTests = ref([]);

    return { testSelectionTable, seletedTests, testGroup, testGroupsOptions };
}
